<template>
   <div>
    <h1>MQTT Node.js</h1>
    <h2>Publisher</h2>
    <div class="line"></div>
    <form @submit.prevent="publishMessage" id="publishForm">
      <ul class="wrapper">
        <li class="form-row">
          <label for="topic">Topic</label>
          <input type="text" id="topic" v-model="topic" class="input-field" />
        </li>
        <li class="form-row">
          <label for="message">Message</label>
          <input type="text" id="message" v-model="message" class="input-field" />
        </li>
        <li class="form-row">
          <button type="submit" class="publish-button">Publish</button>
        </li>
      </ul>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      topic: '',
      message: ''
    };
  },
  methods: {
    publishMessage() {
      axios.post('/publisher', {
        topic: this.topic,
        message: this.message
      })
      .then(response => {
        console.log(response.data);
      })
      .catch(error => {
        console.error('Error:', error);
      });
    }
  }
};
</script>

<style>
.form-row > input {
  flex: 2;
}

.form-row > input,
.form-row > button {
  padding: 1em;
  margin: 10px 0;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  font-size: 1.2em;
}
.form-row > button {
  background-color: #3f51b5;
  color: white;
  border: 0;
  flex: 3;
  font-weight: 600;
}
</style>
