<template>
   <div>
    <h1>MQTT Node.js</h1>
    <h2>Subscriber</h2>
    <div class="line"></div>
    <ul class="wrapper">
      <li class="form-row">
        <label for="topic">Topic</label>
        <input type="text" id="topic" v-model="topic" />
      </li>
      <li class="form-row">
        <label for="message">Message</label>
        <textarea id="message" v-model="message" rows="10" readonly></textarea>
      </li>
      <li class="form-row">
        <label for="status">Status</label>
        <input type="text" id="status" v-model="status" readonly />
      </li>
      <li class="form-row">
        <div class="btn-container">
          <button type="button" @click="subscribe" id="subscribe">Subscribe</button>
          <button type="button" @click="unsubscribe" id="unsubscribe">Unsubscribe</button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      messages: []
    };
  },
  created() {
    // Call a method to fetch messages when the component is created
    this.fetchMessages();
  },
  methods: {
    fetchMessages() {
      // Make a request to fetch messages from the server
      // For example, you could use axios to make an HTTP GET request
      // Replace the URL with your actual endpoint
      axios.get('/messages')
        .then(response => {
          this.messages = response.data;
        })
        .catch(error => {
          console.error('Error:', error);
        });
    }
  }
};
</script>

<style>
.form-row > input,
.form-row > textarea {
  flex: 2;
}
.form-row > input,
.form-row > button,
.form-row > textarea {
  padding: 0.5em;
  resize: vertical;
  margin: 10px 0;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  font-size: 1.2em;
}
.form-row > textarea {
  font-size: 1.5em;
  background-color: rgb(242, 248, 248);
  text-align: center;
  font-weight: bold;
}
.btn-container {
  flex: 3;
  column-gap: 10px;
  display: flex;
}
.btn-container > button {
  width: 50%;
  border: 0;
  padding: 1em;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  color: white;
  font-size: 1.2em;
}

#subscribe {
  background-color: green;
}
#unsubscribe {
  background-color: rgb(176, 6, 6);
}

#status {
  background-color: lightcyan; 
  text-align: center;
  font-weight: bold;
}

</style>
